const condition1 = document.querySelector('a')


export const ViewProject = ({svgShow, svgHide, svgToggle, gridLetter}) => {

// get the link that fits the clicked element

function handleLink() {
    var filtered = {}
    for(var key in projectLinks) { 
      if(key[0] === gridLetter){
        filtered[key] = projectLinks[key]
      }
    }
    let link = Object.values(filtered)
console.log(link);        
return link

}
        
       

    
  function handleMouseOver(e) {
        let a = e.target;
        let anim = () => {
            if (a.offsetHeight === 109) {
              svgShow()
              a.removeEventListener('transitionend', anim)
            }
            }
            a.addEventListener('transitionend', anim)


    }
  

        return (
                <div className='viewDiv'
                    >
                            <a 
                                onMouseOver={handleMouseOver}
                                onMouseLeave={svgHide}
                                href={handleLink()}
                                target='_blank'                                    
                                className="viewProject"
                                >view project
                            </a>
                            <svg className={svgToggle ? 'svgShow' : 'hidden'}
                            // How to make this fade in nicely && remember that one of the wrappers has 100% width!
                                id='codepen' xmlns="https://www.w3.org/2000/svg" viewBox="-380 0 900 26" fill="none" stroke="#000" stroke-width="2.3" stroke-linecap="round" stroke-linejoin="round">
                                <path d="M80 6h-9v14h9 M114 6h-9 v14h9 M111 13h-6 M77 13h-6 M122 20V6l11 14V6 M22 16.7L33 24l11-7.3V9.3L33 2L22 9.3V16.7z M44 16.7L33 9.3l-11 7.4 M22 9.3l11 7.3 l11-7.3 M33 2v7.3 M33 16.7V24 M88 14h6c2.2 0 4-1.8 4-4s-1.8-4-4-4h-6v14 M15 8c-1.3-1.3-3-2-5-2c-4 0-7 3-7 7s3 7 7 7 c2 0 3.7-0.8 5-2 M64 13c0 4-3 7-7 7h-5V6h5C61 6 64 9 64 13z"/>
                                </svg>
                        </div>
        )

        }
